.pop-up__tooltip-text {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #000000;
  margin: 0 0 23px 0;
}

@media screen and (max-width: 568px) {
  .pop-up__tooltip-text {
    font-size: 20px;
    line-height: 24px;
    margin-top: 25px;
  }
}
